.form {
    display: block;
 }
 .form__header {
     text-align: center;
 }
 
 .form__labelText {
     width: 100%;
     max-width: 250px;
     display: inline-block;
     margin-right: 10px;
     padding: 10px 0;
 }
 
 .form__field {
     border: 1px solid hsl(0, 0%, 70%);
     padding: 10px;
     width: 100%;
     max-width: 300px;
     text-align: center;
     border-radius: 5px;
     display: inline-block;
     font-weight: bold;
 }
 
 .form__field:invalid {
     background-color: hsl(348, 86%, 99%);
 }
 
 .form__button {
     width: 100%;
     border: none;
     padding: 10px;
     background-color: hsl(75, 10%, 40%);
     border-radius: 5px;
     color: whitesmoke;
 }
 
 .form__button:hover {
     background-color: hsl(75, 10%, 45%);
 }
 
 .form__button:active {
     background-color: hsl(75, 10%, 50%);
 }
 
 .form__result {
     text-align: center;
     font-size: 20px;
 }